const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Label)
    label: cc.Label = null;
    
    typeTranslate: string = 'rightToLeft'
    speed: number = 100
    isMoving: boolean = false
    maxFrameWidth: number = 640

    // @property
    // distance: number = 0


    onKeyDown(e) {
        switch(e.keyCode) {
            case cc.macro.KEY.d:
                // console.log('press d');
                this.typeTranslate = 'leftToRight'
                this.isMoving = true
                break

            case cc.macro.KEY.a:
                // console.log('press a');
                this.typeTranslate = 'rightToLeft'
                this.isMoving = true
                break
        }
    }

    onKeyUp(e) {
        switch(e.keyCode) {
            case cc.macro.KEY.d:
            case cc.macro.KEY.a:
                this.isMoving = false
        }
    }

    // LIFE-CYCLE CALLBACKS:

    start () {
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN , this.onKeyDown, this)
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP , this.onKeyUp, this)
    }

    update (dt) {
        if(this.isMoving) {
            if(this.typeTranslate === 'leftToRight') {
                const translateX = this.node.position.x - this.speed * dt
                // const scaleX = this.node.scaleX
                // const maxTranslateX = -(this.maxFrameWidth / 2) - (this.node.width * scaleX / 2)
                this.node.setPosition(translateX, this.node.position.y)
                
                // if(translateX < maxTranslateX) {
                if(translateX < -(this.maxFrameWidth * 2)) {
                    
                //     //khi mà trượt hết khỏi rìa khung bên trái
                //     //làm cho node này xuất hiện ở ngoài rìa bên phải cái khung
                    console.log('aaaaaaaa');
                //     // this.node.setPosition(-(maxTranslateX))
                //     // const x = this.maxFrameWidth / 2 + (this.node.width * scaleX / 2) + this.distance
                //     // const x = this.maxFrameWidth / 2 + this.distance
                //     // console.log(x);
                //     this.node.setPosition(this.maxFrameWidth, this.node.position.y)
                this.node.setPosition(-this.maxFrameWidth, this.node.position.y)

                }

            }
            else {
                const translateX = this.node.position.x + this.speed * dt
                // const scaleX = this.node.scaleX
                // const maxTranslateX = (this.maxFrameWidth / 2) + (this.node.width * scaleX / 2)
                this.node.setPosition(translateX, this.node.position.y)
                // if(translateX > maxTranslateX) {
                    console.log(translateX);
                    
                if(translateX > 0) {
                    //when node translate to the end of right side
                    console.log('bbbbbbbb');
                    // this.node.setPosition(maxTranslateX, this.node.position.y)
                    //khi trượt hết khỏi rìa bên phải, node này sẽ xuất hiện ở rìa bên trái
                    // this.node.setPosition(-maxTranslateX)
                    // const x = -(this.maxFrameWidth / 2) - (this.node.width * scaleX / 2) - this.distance
                    // const x = -(this.maxFrameWidth / 2) - this.distance
                    // console.log(x);
                    this.node.setPosition(-this.maxFrameWidth, this.node.position.y)
                }
            }
        }
    }
}
